<template>
  <div class="Account-details">
    <div class="Account-content">
      <el-card class="box-card">
        <div slot="header" class="clearfix">
          <span>管理员信息</span>
        </div>
        <div class="text item">
          <ul class="Account-ul">
            <li>管理员ID：{{tableData.id}}</li>
            <li>账号：{{tableData.account}}</li>
            <li>用户组：{{tableData.userGroup}}</li>
            <li>创建时间：{{tableData.ctime}}</li>
          </ul>
          <div class="nameimg">
            <span>管理员头像：</span>
            <el-upload 
              class="avatar-uploader"
              action="http://127.0.0.1:3333/goods.php?a=uploadImg"
              :show-file-list="false"
              :on-success="handleAvatarSuccess"
              :before-upload="beforeAvatarUpload"
           >
              <img v-if="imageUrl" :src="imageUrl" class="avatar" />
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </div>
          <div style="clear:both;"></div>
        </div>
      </el-card>
    </div>
  </div>
</template>

<script>
// import axios from "axios"
export default {
  async created() {
    let data = await this.$axios.get(`users.php?a=readOne&id=${sessionStorage.getItem("id")}`
    );
    // console.log(data);
    if (data.data.code == 0) {
      this.tableData = data.data.data;
      this.imageUrl= data.data.data.imgUrl
    }
  },
  data() {
    return {
      tableData: {
        id: 123,
        name: "王小虎",
        account: "超级管理员",
        datatime: "2020.3.3"
      },
     imageUrl:'',
    };
  },
 methods: {//修改头像
  async handleAvatarSuccess(res) {
        // this.imageUrl = URL.createObjectURL(file.raw);
        this.imageUrl = "http://127.0.0.1:3333/"+res.data;
        let imgobj={
          id:this.tableData.id,
          imgUrl:this.imageUrl
        }
        await this.$axios.post('users.php?a=chgImg',this.qs.stringify(imgobj))
      
      },
      beforeAvatarUpload(file) {
        const isJPG = file.type === 'image/jpeg';
        const isLt2M = file.size / 1024 / 1024 < 2;

        if (!isJPG) {
          this.$message.error('上传头像图片只能是 JPG 格式!');
        }
        if (!isLt2M) {
          this.$message.error('上传头像图片大小不能超过 2MB!');
        }
        return isJPG && isLt2M;
      }
    }
  }

</script>

<style >
.Account-content {
  width: 100%;
  height: 100%;
  background: #ffffff;
}

.Account-details .Account-ul {
  text-align: left;
  list-style: none;
}
.Account-details .Account-ul > li {
  line-height: 80px;
  border-bottom: 1px solid #e7e9ee;
  background: #ffffff;
  font-size: 18px;
  padding-left: 30px;
}
.Account-details .nameimg {
  float: left;
  margin-left: 30px;
}
.Account-details .nameimg > span:first-child {
 line-height: 180px;
  font-size: 18px;
}
.Account-details .nameimg {
  margin: 46px 0 16px 32px;
}


.avatar-uploader{
  float: right;
}
.avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
</style>